import React from 'react';
import styled from 'styled-components';
import { IconClose } from '../../basic/icon';
import { TAB_MARGIN, TAB_BOTTOM_MARGIN } from './constants';
import { mergeClassName, COLORS } from '../../common';
import type { ITabPanelProps } from './TabPanel';

const TabsTabWrapper = styled.div`
  position: relative;
  padding-right: ${TAB_MARGIN}px;
  margin-bottom: ${TAB_BOTTOM_MARGIN}px;
  cursor: default;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  user-select: none;
  color: ${COLORS.LIGHT.LEVEL2_BASE_COLOR};
  &:hover {
    color: ${COLORS.LIGHT.LEVEL1_BASE_COLOR};
    .wdn-tabs-tab-close-wrapper {
      display: block;
      cursor: pointer;
    }
  }
  &.wdn-tabs-tab-active {
    color: ${COLORS.LIGHT.LEVEL1_BASE_COLOR};
    text-shadow: 0 0 .25px currentcolor;
  }
  &.wdn-tabs-tab-disabled {
    color: rgba(23,26,29,0.24) !important;
    cursor: not-allowed !important;
  }
`;

const TitleWrapper = styled.span`
  display: inline-block;
`;

const TabsTabContent = styled.div`
  display: flex;
  align-items: center;
  padding: 8px 0;
`;

const CloseWrapper = styled.div`
  display: none;
  position: absolute;
  top: 50%;
  right: ${TAB_MARGIN}px;
  transform: translate(100%, -50%);
  font-size: 0;
  line-height: 0;
  .wdn-icon {
    width: 16px;
    height: 16px;
  }
`;

const TabsTabPrefix = styled.div`
  display: flex;
  align-items: center;
  margin-right: 6px;
`;

type ITabsBarTabProps = {
  activeKey: string;
  onClick: (key: string) => void;
  tabKey: string;
  onRef: (key: string, elem: HTMLDivElement) => void;
} & ITabPanelProps;

export const TabsBarTab: React.FC<ITabsBarTabProps> = (props) => {
  const { activeKey, onClose, title, tabKey, prefix, onClick, onRef, disabled } = props;
  const isActive = activeKey === tabKey;

  const handleClick = React.useCallback((e: React.MouseEvent) => {
    if (disabled) {
      return;
    }
    onClick(tabKey);
  }, [tabKey, disabled]);

  const handleRef = React.useCallback((elem: HTMLDivElement) => {
    onRef(tabKey, elem);
  }, [tabKey]);

  return (
    <TabsTabWrapper 
      className={mergeClassName(isActive ? 'wdn-tabs-tab-active' : '', disabled ? 'wdn-tabs-tab-disabled' : '', 'wdn-tabs-tab-wrapper')}
    >
      <TabsTabContent onClick={handleClick} ref={handleRef} data-tab-key={tabKey}>
        { prefix && (
            <TabsTabPrefix>
              {prefix}
            </TabsTabPrefix>
            )
        }
        <TitleWrapper>
          {title}
        </TitleWrapper>
      </TabsTabContent>
      {
        onClose && (
          <CloseWrapper className='wdn-tabs-tab-close-wrapper' onClick={onClose}>
            <IconClose style={{ fontSize: 8 }}/>
          </CloseWrapper>
        )
      }
    </TabsTabWrapper>
  )
};
